import React, { useState, useEffect } from "react";
import Layout from "@/components/Layout";
import { Button, Table, Modal, Form, InputGroup, FormControl } from "react-bootstrap";
import styles from "../styles/shipping.module.css"; // Custom styles (CSS)
import axios from "axios"; // For making API calls
import PaginationComponent from "@/components/PaginationComponent"; // Custom Pagination Component

const ShippingPage = () => {
  const [showModal, setShowModal] = useState(false);
  const [shipmentData, setShipmentData] = useState({});
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1); // State for current page
  const [itemsPerPage] = useState(3); // Number of orders per page

  // Static data for orders (simulate an API response)
  const staticOrders = [
    { id: 1, recipientName: "John Doe", address: "123 Main St, City", status: "Ready to Ship" },
    { id: 2, recipientName: "Jane Smith", address: "456 Oak Ave, Town", status: "Ready to Ship" },
    { id: 3, recipientName: "Mark Lee", address: "789 Pine Rd, Village", status: "Shipped" },
    { id: 4, recipientName: "Emily Davis", address: "101 Maple Dr, Suburb", status: "Delivered" },
    { id: 5, recipientName: "Alex Green", address: "567 Birch Blvd", status: "Ready to Ship" },
    { id: 6, recipientName: "Chris White", address: "432 Cedar St", status: "Shipped" },
    // Add more static data for pagination demonstration
  ];

  // Fetch orders and handle pagination
  const fetchOrders = async () => {
    try {
      setLoading(true);
      // Simulating a delay, as if we were fetching data from an API
      setTimeout(() => {
        setOrders(staticOrders.filter(order => order.status === "Ready to Ship"));
        setLoading(false);
      }, 1000); // Simulate 1 second delay
    } catch (error) {
      console.error("Error fetching orders:", error);
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchOrders();
  }, []);

  // Handle form input changes
  const handleChange = (e) => {
    const { name, value } = e.target;
    setShipmentData((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  // Handle modal close
  const handleCloseModal = () => {
    setShowModal(false);
    setShipmentData({}); // Clear data after closing modal
  };

  // Handle form submission and create shipment
  const handleSubmit = async (e) => {
    e.preventDefault();

    // Prepare shipment data (mocking Shiprocket API)
    const shipmentPayload = {
      recipientName: shipmentData.recipientName,
      address: shipmentData.address,
      status: shipmentData.status,
      orderId: shipmentData.orderId, // Link the order ID
    };

    try {
      // Send the shipment data to your Shiprocket API
      const response = await axios.post('/api/shiprocket', shipmentPayload);

      // After successful shipment creation, get the tracking number from the response
      const trackingNumber = response.data.tracking_number;

      // Update the local shipment list with the tracking number
      setOrders((prevOrders) => [
        ...prevOrders,
        { ...shipmentPayload, trackingNumber, id: prevOrders.length + 1 },
      ]);

      // Optionally, update the status of the order to 'Shipped'
      setOrders((prevOrders) =>
        prevOrders.map((order) =>
          order.id === shipmentData.orderId ? { ...order, status: "Shipped" } : order
        )
      );

      handleCloseModal();
    } catch (error) {
      console.error("Error creating shipment:", error);
    }
  };

  // Handle changing order status
  const handleOrderStatusChange = (orderId, status) => {
    setOrders((prevOrders) => prevOrders.map(order =>
      order.id === orderId ? { ...order, status } : order
    ));
  };

  // Pagination logic for the orders
  const totalPages = Math.ceil(orders.length / itemsPerPage);

  const paginatedOrders = orders.slice(
    (currentPage - 1) * itemsPerPage,
    currentPage * itemsPerPage
  );

  return (
    <Layout>
      <div className="container mt-5">
        <h1 className={`${styles.pageTitle} text-center mb-4`}>Shipping Management</h1>

        {/* Search and Add Buttons */}
        <div className="row mb-4">
          <div className="col-md-6">
            <InputGroup>
              <FormControl placeholder="Search by recipient or address" />
            </InputGroup>
          </div>
          <div className="col-md-6 d-flex justify-content-end">
            <Button variant="primary" onClick={() => setShowModal(true)}>
              Create Shipment
            </Button>
          </div>
        </div>

        {/* Table for displaying orders */}
        <Table striped bordered hover>
          <thead>
            <tr>
              <th>#</th>
              <th>Order ID</th>
              <th>Recipient Name</th>
              <th>Address</th>
              <th>Status</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {loading ? (
              <tr>
                <td colSpan="6" className="text-center">
                  Loading...
                </td>
              </tr>
            ) : (
              paginatedOrders.map((order, index) => (
                <tr key={order.id}>
                  <td>{index + 1}</td>
                  <td>{order.id}</td>
                  <td>{order.recipientName}</td>
                  <td>{order.address}</td>
                  <td>{order.status}</td>
                  <td>
                    <Button
                      variant="info"
                      size="sm"
                      onClick={() => {
                        setShipmentData({ orderId: order.id, recipientName: order.recipientName, address: order.address });
                        setShowModal(true);
                      }}
                    >
                      Create Shipment
                    </Button>
                    {order.status === "Ready to Ship" && (
                      <Button
                        variant="success"
                        size="sm"
                        onClick={() => handleOrderStatusChange(order.id, "Shipped")}
                        className="ml-2"
                      >
                        Mark as Shipped
                      </Button>
                    )}
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </Table>

        {/* Pagination Component */}
        <PaginationComponent
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={setCurrentPage}
        />

        {/* Modal for creating/editing shipment */}
        <Modal
          show={showModal}
          onHide={handleCloseModal}
          className={styles.customModal}
          centered
        >
          <Modal.Header closeButton>
            <Modal.Title>Create/Edit Shipment</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form onSubmit={handleSubmit}>
              <Form.Group controlId="recipientName" className="mb-3">
                <Form.Label>Recipient Name</Form.Label>
                <Form.Control
                  type="text"
                  name="recipientName"
                  value={shipmentData.recipientName || ""}
                  onChange={handleChange}
                  placeholder="Enter recipient name"
                />
              </Form.Group>

              <Form.Group controlId="address" className="mb-3">
                <Form.Label>Shipping Address</Form.Label>
                <Form.Control
                  type="text"
                  name="address"
                  value={shipmentData.address || ""}
                  onChange={handleChange}
                  placeholder="Enter shipping address"
                />
              </Form.Group>

              <Form.Group controlId="status" className="mb-3">
                <Form.Label>Status</Form.Label>
                <Form.Select
                  name="status"
                  value={shipmentData.status || ""}
                  onChange={handleChange}
                >
                  <option value="">Select Status</option>
                  <option value="Processing">Processing</option>
                  <option value="Shipped">Shipped</option>
                  <option value="Delivered">Delivered</option>
                  <option value="Canceled">Canceled</option>
                </Form.Select>
              </Form.Group>

              <Button
                variant="primary"
                type="submit"
                className={styles.modalButtonPrimary}
              >
                Save Shipment
              </Button>
            </Form>
          </Modal.Body>
        </Modal>
      </div>
    </Layout>
  );
};

export default ShippingPage;
